.CSuper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(../../images/bgclientes.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

.CContainer {
  display: flex;
  width: 80%;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
  margin-top: 100px;
  margin-bottom: 100px;
}

.CContainer h1 {
  font-size: 52px;
  font-weight: bold;
  color: var(--color2);
  text-align: center;
}

.CContainer span {
  color: var(--bgcolor2);
}

.CImages {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;

  /* border: solid blue 1px; */
}

.CImages img {
  margin: 35px;
}

/*==========Media Queries===========*/

@media screen and (max-width: 768px) {
  .CContainer h1 {
    font-size: 32px;
    margin-top: 50px;
    margin-bottom: 50px;
  }
}
@media screen and (max-width: 425px) {
  .CImages {
    justify-content: center;
  }
}

@media screen and (max-width: 375px) {
  .CImages img {
    margin: 5px;
  }
}
