.FSuper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(../../images/bgfooter.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  position: relative;

  /* height: 600px; */
}

.FContent {
  display: flex;
  justify-content: center;
  align-items: start;
  width: 90%;
  margin-top: 100px;
  margin-bottom: 100px;
}

.FContactInfo {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  width: 50%;
}

.FTitle {
  width: 100%;

  /* border: solid red 1px; */
}

.FTitle h1 {
  font-size: 52px;
  font-weight: normal;
  color: var(--bgcolor1);
  text-align: left;
  margin-left: 20px;
}

.FLinks {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
}

.FLink {
  display: flex;
  justify-content: center;
  align-items: center;
}

.FATag {
  display: flex;
  flex-direction: row;
  align-items: center;
  /* border-bottom: solid white 4px; */
}

.FLink h2 {
  color: var(--bgcolor1);
  font-weight: normal;
  font-size: 32px;
}

.FLink img {
  height: 70px;
  margin: 20px;
}

.FLogo {
  position: absolute;
  bottom: 50px;
  right: 100px;
}

.FLogo img {
  height: 130px;
}

/*==========Media Queries===========*/

@media screen and (max-width: 1024px) {
  .FTitle h1 {
    font-size: 32px;
  }

  .FLink h2 {
    font-size: 20px;
  }

  .FLogo img {
    height: 70px;
  }

  .FLink img {
    height: 50px;
    margin: 20px;
  }
}

@media screen and (max-width: 768px) {
  .FContent {
    flex-direction: column;
  }

  .FLogo {
    position: absolute;
    bottom: 20px;
    right: 20px;
  }
}

@media screen and (max-width: 375px) {
  .FLink h2 {
    font-size: 16px;
  }
}
