.PSuper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(../../images/bgpartner.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

.PContent {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 90%;
  margin-top: 100px;
  margin-bottom: 100px;
}

.PTitle h1 {
  font-size: 52px;
  font-weight: bold;
  color: var(--color1);
}

.PTitle span {
  color: var(--bgcolor2);
}

.PLogos {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}

.PLogos img {
  margin: 50px;
}

.PCollabs {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}

.PCollabs img {
  margin: 50px;
}

/*==========Media Queries===========*/

@media screen and (max-width: 768px) {
  .PTitle h1 {
    font-size: 32px;
    margin-top: 50px;
    margin-bottom: 50px;
  }
}
