.NSuper {
  position: fixed;
  height: 190px;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  transition: 0.3s ease-in;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(243, 243, 243, 0.8);

  /* border: solid red 2px; */
}

.NSuper-alt {
  height: 80px;
}

.NContent {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.NLogo img {
  cursor: pointer;
  height: 140px;
  transition: 0.3s ease-in;
}

.NLogo-alt img {
  height: 70px;
}

.NNavigation {
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.NNavigation p {
  font-size: 28px;
  transition: 0.3s ease-in;
  padding-left: 5px;
  border-bottom: solid rgba(0, 0, 0, 0) 4px;
  border-left: solid rgba(0, 0, 0, 0) 4px;
}

.NNavigation p:hover {
  border-bottom: solid var(--color2) 4px;
  border-left: solid var(--color1) 4px;
  cursor: pointer;
}

.NNavigation span {
  color: var(--color1);
}

.NHamburger {
  display: none;
}

.NRNavigation {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* height: 500px; */
  padding-top: 50px;
  padding-bottom: 50px;
  width: 100%;
  background-color: var(--bgcolor1);
  position: fixed;
  top: 190px;
  right: 0px;
  /* transition: all 0.5s ease-in-out; */
}

.NRN-alt {
  top: 80px;
}

.NRNavigation p {
  font-size: 28px;
  transition: 0.3s ease-in;
  padding-left: 5px;
  border-bottom: solid rgba(0, 0, 0, 0) 4px;
  border-left: solid rgba(0, 0, 0, 0) 4px;
}

.NRNavigation p:hover {
  border-bottom: solid var(--color2) 4px;
  border-left: solid var(--color1) 4px;
  cursor: pointer;
}

.NRNavigation span {
  color: var(--color1);
}

.NRN-off {
  /* display: none; */
  right: 2600px;
}

/*===========Media Queries===========*/
/*1440px*/

@media screen and (max-width: 1440px) {
  .NHamburger {
    display: flex;
  }

  .NNavigation {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .NLogo img {
    height: 100px;
  }

  .NLogo-alt img {
    height: 70px;
  }
}
